@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
html{box-sizing:border-box}*,*::after,*::before{box-sizing:inherit}body{margin:0;padding:0;line-height:1;font-family:"Roboto", sans-serif;color:#202020;background-color:#fafafa}.header{border-bottom:solid 1px #d79771;background-color:#c3ba85;height:54px;width:100%}.header img{width:auto;height:44px}.header nav{height:100%;max-width:922px;display:flex;align-items:center;margin:auto;justify-content:center}.content{display:flex;max-width:922px;margin:auto}.sidebar{-webkit-user-select:none;-ms-user-select:none;user-select:none;width:266px;height:100vh;padding-top:74px;border-right:1px solid #f1f1f1;background-color:#f0f0cb}.sidebar div{margin-bottom:10px;cursor:pointer;padding:15px}.sidebar div.active{background-color:#dad5ab}.sidebar div .icon{margin-right:10px}.tasks{background-color:white;height:100vh;width:calc(922px - 266px);padding:74px 20px 0}.tasks .add-task-btn{cursor:pointer}.tasks .add-task-btn .plus{color:#c49e9e;font-size:10px}.tasks .add-task-btn .add-task-text{color:black;font-size:14px;margin-left:15px}.tasks .add-task-dialogue{margin-top:10px}.tasks .add-task-dialogue input{width:100%;border:1px solid #ddd;background-color:white;border-radius:3px;height:35px;padding-left:10px;padding-right:10px}.tasks .add-task-dialogue .add-task-action-container{margin-top:10px;display:flex;justify-content:space-between}.tasks .add-task-dialogue .add-task-action-container .add-btn{background-color:#f0f0cb;color:#000;border:1px solid #d9cab3;margin-right:5px;font-weight:bold;font-size:13px;padding:6px 12px 7px 12px;border-radius:3px !important}.tasks .add-task-dialogue .add-task-action-container .add-btn:disabled{background-color:gray;color:white}.tasks .add-task-dialogue .add-task-action-container .add-btn:active{-webkit-transform:scale(0.85);transform:scale(0.85)}.tasks .add-task-dialogue .add-task-action-container .cancel-btn{all:unset;color:#555;cursor:pointer;font-size:14px;margin:0 5px}.tasks .tasks-item-container{margin-top:40px;padding:0 20px}.tasks .tasks-item-container .task-item{display:flex;justify-content:space-between}.tasks .tasks-item-container .task-item:not(:last-child){border-bottom:1px solid #c3ba85}

